<template>
  <div>
    <OtherPageSlide :title="'پروژه ها'" :desc="'پروژه خوب چی میتونه باشه ؟'">
      <span slot="bc">
        <router-link :to="{ name: 'MainPage' }" custom v-slot="{ navigate, href }" tag="a"><a :href="href"
            @click="navigate">خانه</a></router-link> / تیم ما
      </span>
    </OtherPageSlide>
    <v-container class="py-16">
      <v-row>
        <v-col v-if="$route.params.id" cols="12">
          <v-row>
            <!-- <v-col cols="12" sm="6" md="3" lg="2">
              <v-text-field v-model="filters.name" label="نام پروژه"></v-text-field>
            </v-col> -->
            <v-col cols="12" sm="6" md="3" lg="2">
              <v-combobox label="استان" :items="provinces" @change="fetchCities" v-model="filters.province"></v-combobox>
            </v-col>
            <v-col cols="12" sm="6" md="3" lg="2">
              <v-combobox label="شهر" :items="cities" v-model="filters.city"></v-combobox>
            </v-col>
            <v-col cols="12" sm="6" md="3" lg="2">
              <v-combobox label="نوع" :items="types" v-model="filters.productType"></v-combobox>
            </v-col>
            <v-col cols="12" sm="6" md="3" lg="2">
              <v-text-field id="start_date" append-icon="mdi-calendar-range" label="تاریخ شروع"
                v-model="filters.startDate"></v-text-field>
              <date-picker v-model="filters.startDate" format="YYYY-MM-DD hh:mm" display-format="jYYYY/jMM/jDD"
                element="start_date" :clearable="true" />
            </v-col>
            <v-col cols="12" sm="6" md="3" lg="2">
              <v-text-field id="end_date" append-icon="mdi-calendar-range" label="تاریخ پایان"
                v-model="filters.endDate"></v-text-field>
              <date-picker v-model="filters.endDate" format="YYYY-MM-DD hh:mm" display-format="jYYYY/jMM/jDD"
                element="end_date" :clearable="true" />
            </v-col>
            <v-col cols="12" sm="6" md="3" lg="2">
              <v-text-field label="کف قیمت" type="number" v-model="filters.price_lte"></v-text-field>
            </v-col>
            <v-col cols="12" sm="6" md="3" lg="2">
              <v-text-field label="سقف قیمت" type="number" v-model="filters.price_gte"></v-text-field>
            </v-col>
            <v-col cols="12" sm="6" md="3" lg="2">
              <v-text-field label="کف مساحت زیر بنا" v-model="filters.base_lte"></v-text-field>
            </v-col>
            <v-col cols="12" sm="6" md="3" lg="2">
              <v-text-field label="سقف مساحت زیر بنا" v-model="filters.base_gte"></v-text-field>
            </v-col>
            <v-col cols="12" sm="6" md="3" lg="2" class="d-flex align-center">
              <v-btn elevation="0" color="primary" dark class="rounded-0" width="100%" @click.prevent="search()"> جستجو
                کردن </v-btn>
            </v-col>
          </v-row>
        </v-col>

        <v-col cols="12" id="topPrjs">
        </v-col>

        <v-col cols="12" v-if="$route.query.q">
          <h3 class="font-weight-regular">نتایج جستجو بر اساس : <strong>"{{ $route.query.q }}"</strong></h3>
        </v-col>

        <v-col v-for="(item, index) in data.data" :key="index + 'pList'" cols="12" :sm="$route.params.id ? 12 : 6"
          :md="$route.params.id ? 12 : 4" :lg="$route.params.id ? 12 : 3">
          <v-skeleton-loader v-if="loading" width="100%" type="card"></v-skeleton-loader>
          <div v-else>
            <ProductCardItemList v-if="(!mobileDisplay) && ($route.params.id)" :item="item" />
            <ProductCardItem v-else :item="item" />

          </div>
        </v-col>
        <v-col v-if="data && data.data && data.data.length === 0" cols="12">
          <h4 class="text-center">موردی یافت نشد.</h4>
        </v-col>
        <v-col cols="12">
          <div class="text-center my-6">
            <p class="text-center mb-2 primary--text" v-if="data.total_objects">{{ data.total_objects }} پروژه</p>
            <v-pagination v-if="data.total_objects" :value="options.page" v-model="options.page" color="primary"
              :length="parseInt(data.total_pages)"></v-pagination>
          </div>
        </v-col>
      </v-row>
    </v-container>

  </div>
</template>

<script>
import OtherPageSlide from "@/components/Misc/OtherPageSlide";
import ProductCardItem from "@/components/ProductList/ProductCardItem";
import ProductCardItemList from "@/components/ProductList/ProductCardItemList";
import axios from "axios";
import { datesChanges } from "@/components/Misc/datesChanges";
import datePicker from 'vue-persian-datetime-picker'


export default {
  name: "TeamPage",
  components: { ProductCardItem, OtherPageSlide, ProductCardItemList, datePicker },
  created() {
    this.$store.state.loading = true;
    this.checkQueries();
    this.fetchProjects();
    if (this.$route.params.id) {
      this.fetchProvinces();
      this.fetchTypes();
    }
    // this.fetchState();
  },
  mixins: [datesChanges],
  computed: {
    mobileDisplay() {
      switch (this.$vuetify.breakpoint.name) {
        case 'lg': return false
        case 'xl': return false
        default: return true
      }
    },
  },

  data() {
    return {
      name: null,
      tab: null,
      states: null,
      provinces: [],
      cities: [],
      types: [],
      options: {
        page: 1,
        ordering: '-id',
        category: null,
      },
      filters: {
        name: null,
        province: null,
        city: null,
        productType: null,
        startDate: null,
        endDate: null,
        price_lte: null,
        price_gte: null,
        base_lte: null,
        base_gte: null
      },
      data: null,
      loading: false,
    }
  },
  watch: {
    '$route'() {
      this.checkQueries();
      this.fetchProjects();
      if (this.$route.params.id) {
        this.fetchProvinces();
        this.fetchTypes();
      }
    }
  },
  methods: {
    search() {
      console.log(this.filters);
      this.$router.push({
        name: 'ProjectsListId', params: { id: this.$route.params.id }, query: {
          price__gte: this.filters.price_gte,
          price__lte: this.filters.price_lte,
          date_expire_date__lte: this.filters.endDate,
          date_const_date__gte: this.filters.startDate,
          product_type: this.filters.productType ? this.filters.productType.value : null,
          location: this.filters.city ? this.filters.city.value : null,
          location__province: this.filters.province ? this.filters.province.value : null,
          foundation__lte: this.filters.base_lte,
          foundation__gte: this.filters.base_gte,
          search: this.filters.name
        }
      }).catch(() => { })
    },
    fetchState() {
      axios.get(this.$store.state.host + '/projects/api/v1/category/categories', {
        headers: {
          Accept: "application/json",
          // Authorization: `Bearer ${this.$store.state.userId}`,
        },
      }).then(response => {
        this.states = response.data.data;
      }).catch(error => {
        if (error.response.status == 401) {
          this.$router.push({ name: 'Login' });
        } else {
          this.errorHandle(error.response)
        }
      })
    },
    checkQueries() {
      if (this.$route.query.page) {
        this.options.page = parseInt(this.$route.query.page);
      }
      if (this.$route.query.q) {
        this.options.search = this.$route.query.q;
      }
      if (this.$route.params.id) {
        this.options.category = this.$route.params.id;
      } else {
        this.options.category = null;
      }
    },
    fetchProjects() {
      this.loading = true;
      axios.get(this.$store.state.host + '/projects/api/v1/product/products', {
        params: {
          ...this.options,
          price__gte: this.filters.price_gte,
          price_lte: this.filters.price_lte,
          date_expire_date__lte: this.filters.endDate,
          date_const_date__gte: this.filters.startDate,
          product_type: this.filters.productType ? this.filters.productType.value : null,
          location: this.filters.city ? this.filters.city.value : null,
          location_province: this.filters.province ? this.filters.province.value : null,
          foundation_lte: this.filters.base_lte,
          foundation_gte: this.filters.base_gte,
          search: this.filters.name
        },
        headers: {
          Accept: "application/json",
        },
      }).then(response => {
        this.data = response.data;
      }).catch(error => {
        console.log('check')
        if (error.response.status == 401) {
          this.$router.push({ name: 'Login' });
        } else {
          this.errorHandle(error.response)
        }
      }).finally(() => {
        this.loading = false;
        this.$store.state.loading = false;
      })
    },
    fetchProvinces() {
      axios.get(`${this.$store.state.host}/projects/api/v1/province/provinces`)
        .then((response) => {
          this.provinces = response.data.data.map(item => ({ text: item.name, value: item.id }));
        })
        .catch((error) => {
          if (error.response.status == 401) {
            this.$router.push({ name: 'Login' });
          } else {
            this.errorHandle(error.response)
          }
        });
    },
    fetchCities($event) {
      axios.get(`${this.$store.state.host}/projects/api/v1/city/cities/${$event.value}`)
        .then((response) => {
          this.cities = response.data.data.map(item => ({ text: item.name, value: item.id }))
        })
        .catch((error) => {
          if (error.response.status == 401) {
            this.$router.push({ name: 'Login' });
          } else {
            this.errorHandle(error.response)
          }
        });
    },
    fetchTypes() {
      axios.get(this.$store.state.host + '/projects/api/v1/type/types', {
        headers: {
          Accept: "application/json",
          Authorization: `Bearer ${this.$store.state.userId}`,
        },
      }).then(response => {
        console.log(response.data);
        this.types = response.data.data.map(item => ({ text: item.title, value: item.id }));
      }).catch(error => {
        if (error.response.status == 401) {
          this.$router.push({ name: 'Login' });
        } else {
          this.errorHandle(error.response)
        }
      })
    },
  }

}
</script>

<style lang="scss" scoped></style>
